	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/07/crhgrill.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crhgrill.jpg: \" /></p>\n<p>One of the disadvantages of an electric lawn mower is that you have to pay close attention to the power cord. Yesterday Mama didn't pay attention, and mowed right over the cord. Luckily the circuit breaker did break the circuit, and Mama didn't suffer anything more than embarassment (especially now that I've posted this).</p>\n<p>I pulled a muscle or something in my back Thursday night, and I was pretty miserable yesterday. Luckily our neighbor Andrea is a physical therapist. Today I feel like I got beat up, which pretty much describes how Andrea treated my back. At least it's an improvement from yesterday.</p>","fields":{"title":"Cut the cord","date":"07 Jul 2001","url":"https://papascott.de/2001-07-07-cut-the-cord/"}}},"pathContext":{"slug":"/2001-07-07-cut-the-cord/","prev":{"fields":{"title":"Time to relax","date":"03 Jul 2001","slug":"/2001-07-03-time-to-relax/"}},"next":{"fields":{"title":"Up all night","date":"08 Jul 2001","slug":"/2001-07-08-up-all-night/"}}}}

